	module.exports = {"data":{"markdownRemark":{"html":"<p>Lisons <strong>1 Corinthiens 14:2</strong></p>\n<blockquote>\n<p>« En effet, celui qui parle en langue ne parle pas aux hommes, mais à Dieu, car personne ne le comprend, et c’est en esprit qu’il dit des mystères. »</p>\n</blockquote>\n<p>Ce verset est parfois utilisé pour expliquer que celui qui parle en langue s'adresse en réalité à Dieu et que de ce fait, <strong>il est normal que ni lui, ni les personnes présentes ne comprennent ce qu'il dit.</strong></p>\n<p>Lisons néanmoins les versets suivants <strong>1 Corinthiens 14:3-4 :</strong></p>\n<blockquote>\n<p>« Celui qui prophétise, au contraire, parle aux hommes, les édifie, les exhorte, les console. Celui qui parle en langue s'édifie lui-même; celui qui prophétise édifie l'Eglise. »</p>\n</blockquote>\n<p>Paul explique ici que contrairement à celui qui parle en langue, celui qui prophétise édifie l'église car ceux qui l'entendent sont en mesure de comprendre son discours et sont donc exhortés, consolés, édifiés. <strong>La compréhension du message transmit est donc nécessaire afin d'être édifié.</strong></p>\n<p>L'apôtre continue en déclarant que celui qui parle en langue cependant s'édifie lui-même. <strong>Cela signifie donc qu'il est en mesure de comprendre ce qu'il dit !</strong></p>\n<p>Celui qui parle en langue ne parle donc qu'à lui-même et à Dieu car il n'y a que lui-même et Dieu qui sont en mesure de comprendre ce qu'il dit, à moins qu'il y ait un interprète !</p>\n<p>On comprend d'ailleurs en lisant les <strong>versets 27 et 28</strong> que c'est en effet cette idée que Paul exprime au <strong>verset 2</strong>. Paul dira que si personne ne comprend, celui qui parle en langue sans interprète ne peut que se parler littéralement à lui-même (car les autres ne comprennent pas, contrairement à lui) et à Dieu.</p>\n<p>Lisons <strong>1 Corinthiens 14:2-4,27-28 :</strong></p>\n<blockquote>\n<p>« En effet, celui qui parle en langue ne parle pas aux hommes, mais à Dieu, car personne ne le comprend, et c’est en esprit qu’il dit des mystères. Celui qui prophétise, au contraire, parle aux hommes, les édifie, les exhorte, les console. Celui qui parle en langue <strong>s'édifie lui-même;</strong> celui qui prophétise édifie l'Eglise. En est-il qui parlent en langue, que deux ou trois au plus parlent, chacun à son tour, et que quelqu'un interprète; s'il n'y a point d'interprète, qu'on se taise dans l'Eglise, et <strong>qu'on parle à soi-même et à Dieu.</strong> »</p>\n</blockquote>\n<p>Paul ne dit donc pas que le don des langues est incompréhensible aux hommes car il sert à s'adresser à Dieu. D'après ces textes, celui qui exerce le don des langues biblique est en mesure de comprendre ce qu'il dit et Dieu également bien sûr, mais les autres ne peuvent en être édifiés sans un interprète.</p>","frontmatter":{"title":"Mais parler en langues n'est-ce pas parler à Dieu et non aux hommes selon Paul ? Doit-on même comprendre ce que l'on dit ?"}}},"pathContext":{"slug":"/mais-parler-en-langue-n-est-ce-pas-parler-a-dieu-et-non-aux-hommes-selon-paul/"}}

